    <h4 class="fw-bold">A więc wojna...</h4>

    <p>Łukasz Zalesiński
      „Bądźcie bezlitośni, bądźcie brutalni” – apelował do swoich dowódców Adolf Hitler. 1 września 1939 roku o świcie
      niemiecka machina wojenna ruszyła na Polskę rozpoczynając wojnę, która otworzyła w dziejach świata zupełnie nową
      erę. Pierwsze starcie Polacy przegrali, ale zdołali zadać przeciwnikowi znaczące straty.
      Punktualnie o 6:30 program Polskiego Radia został przerwany, zaś w eter popłynął komunikat odczytany przez spikera
      Józefa Małgorzewskiego. „A więc wojna! Z dniem dzisiejszym wszelkie sprawy i zagadnienia schodzą na plan dalszy.
      Całe nasze życie publiczne i prywatne przestawiamy na specjalne tory […]. Cały wysiłek narodu musi iść w jednym
      kierunku. Wszyscy jesteśmy żołnierzami. Musimy myśleć tylko o jednym – walka aż do zwycięstwa!”. Był piątek, 1
      września 1939 roku.
      Niespełna dwie godziny wcześniej na półwysep Westerplatte, gdzie mieściła się polska składnica wojskowa, spadły
      pierwsze pociski wystrzelone z pancernika
        „Schleswig-Holstein”, samoloty Luftwaffe zdążyły już obrócić w perzynę
      przygraniczne miasto Wieluń, a pancerne zagony Wehrmachtu z impetem uderzyły na kilka polskich armii. Tego
      pogodnego
      ranka historia świata wskoczyła na zupełnie nowe tory. Stało się to, czego od dobrych kilku miesięcy spodziewali
      się
      chyba wszyscy.
    </p>
    <h4 class="fw-bold">Pretekst z głowy</h4>
    <p>Adolf Hitler chciał wojny. Kiedy sześć lat wcześniej obejmował władzę, postawił sobie jasny cel: przenicować ład
      wersalski,
      odbudować potęgę Niemiec, na nowo rozrysować mapę Europy – bez Żydów i ze śladową liczbą Słowian
      obsadzonych w roli niewolników. Do celu dążył małymi krokami, testując cierpliwość i determinację zachodnich
      aliantów. A ci pozwalali mu właściwie na wszystko. Wbrew ustaleniom z Wersalu, niemiecka armia puchła w oczach,
      obrastając w nowoczesne wojska pancerne, lotnictwo i marynarkę wojenną. W 1936 roku Hitler zajął zdemilitaryzowaną
      Nadrenię. Dwa lata później wcielił do Rzeszy Austrię. Drogą dyplomatycznych gier i militarnych nacisków
      podporządkował też sobie Czechosłowację. Kolejnym celem stała się Polska.
      Jeszcze w październiku 1938 roku minister spraw zagranicznych Rzeszy Joachim von Ribbentrop złożył polskiemu
      ambasadorowi Józefowi Lipskiemu propozycję dotyczącą m.in. włączenia do Niemiec Wolnego Miasta Gdańska oraz budowę
      eksterytorialnych autostrady i linii kolejowej, które przecinałyby tzw. Korytarz Polski. W zamian zaoferował m.in.
      przedłużenie o 25 lat paktu o nieagresji oraz gwarancję zbytu w Gdańsku polskich towarów. Wiosną 1939 roku
      memorandum do rządu w Warszawie wystosował sam Hitler. Polacy byli już jednak pewni: Rzesza prowadzi grę, która w
      najlepszym razie ma na celu zwasalizowanie sąsiada. Powiedzieli: „nie”. – Polska od Bałtyku odepchnąć się nie da!
      –
      grzmiał z sejmowym expose szef polskiego MSZ Józef Beck. Przygotowania do wojny ruszyły pełną parą.
      <br><br>Plan niemieckiej inwazji otrzymał kryptonim „Fall Weiss”. Wehrmacht miał uderzyć z północy i zachodu, a
      także
      kontrolowanych przez Rzeszę terytoriów Czech i Słowacji. „Zniszczenie Polski jest naszym pierwszym zadaniem. Celem
      musi być nie dotarcie do jakiejś oznaczonej linii, lecz zniszczenie żywej siły. Bądźcie bezlitośni, bądźcie
      brutalni” – apelował Hitler do swoich dowódców podczas narady w ostatnich dniach sierpnia.Po cichu układał się ze
        Związkiem Sowieckim, który miał mu udzielić wojskowego poparcia.<br><br>
      Polscy wojskowi z kolei od marca gorączkowo pracowali nad „Planem Operacyjnym Zachód”. Dla nich perspektywa wojny
      z
      Niemcami była czymś nowym. Od lat za daleko większe zagrożenie uznawali Armię Czerwoną. Teraz planiści zakładali,
      że
      pierwszy etap walk rozegra się wzdłuż granic. Jeśli agresora nie uda się zatrzymać, Polacy będą się bronić na
      linii
      Wisły. Kluczową kwestią w tej układance była jednak postawa sojuszników. Polskie władze liczyły, że kilka dni po
      niemieckiej agresji, do walki włączą się Francji oraz Wielka Brytania, a wzięty w kleszcze Wehrmacht skapituluje.
      Decydenci nie wiedzieli wówczas, że do uderzenia na Polskę szykuje się też Armia Czerwona...
      Latem Niemcy rozpoczęli wzmożone działania dywersyjne. Atakowali posterunki graniczne, zaaranżowali zamach bombowy na
        dworcu
        w
        Tarnowie,próbowali zająć strategiczny
        tunel na
        Przełęczy Jabłonkowskiej.Kulminacją całej serii
      incydentów stała się prowokacja gliwicka , czyli rajd uzbrojonego komanda na radiostację, która znajdowała się... po
      niemieckiej stronie granicy. Naziści odpowiedzialnością za wypad chcieli obarczyć Polaków. Mimo że akcja została
      przeprowadzona nad wyraz nieudolnie, kilkadziesiąt godzin później Hitler wymienił ją jako „casus belli”. Zgodnie
      zresztą ze swoim credo, które na przywołanej już naradzie wyłożył swoim dowódcom: „Dla celów propagandy podam
      jakąś
      przyczynę wybuchu wojny, mniejsza z tym, czy będzie ona wiarygodna, czy nie. Zwycięzcy nikt nie pyta, czy
      powiedział
      prawdę, czy też nie. W sprawach związanych z rozpoczęciem i prowadzeniem wojny nie decyduje prawo, lecz
      zwycięstwo”.
    </p>
    <h4 class="fw-bold"> Pięć tygodni walki</h4>
    <p>25 sierpnia do portu w Gdańsku wpłynął pancernik „Schleswig-Holstein”. Oficjalnie jego załoga miała wziąć udział
      w
      uroczystościach upamiętnienia marynarzy z krążownika „Magdeburg”, który zatonął podczas I wojny światowej.
      Faktycznie okręt sposobił się do ataku na polską składnicę na Westerplatte. Zanim jednak padły pierwsze salwy,
      Niemcy zgodnie z dyplomatycznym ceremoniałem zaprosili na pokład lokalnych decydentów. „Kieliszek szampana przy
      akompaniamencie drwiących uśmieszków smarkatych oficerków. To była najtrudniejsza chwila w moim życiu” – zanotował
      w
      dzienniku Marian Chodacki, sekretarz generalny RP w Gdańsku. 1 września o 4:45 huk okrętowych dział przerwał ciszę
      poranka. Większość historyków utrzymuje, że w tym właśnie momencie rozpoczęła się II wojna światowa.<br><br>
      Do walki przeciwko wschodniemu sąsiadowi Niemcy rzucili w sumie 1,8 miliona żołnierzy, 2,8 tysiąca czołgów i 1,3
      tysiąca samolotów. Polska mogła im przeciwstawić niespełna milion żołnierzy, 880 czołgów i 400 samolotów.
      Strategiczne położenie Rzeczpospolitej było przy tym skrajnie niekorzystne. Granice z terytoriami kontrolowanymi
      przez Rzeszę rozciągały się na blisko trzy tysiące kilometrów, w dodatku w większości miejsc brakowało naturalnych
      przeszkód i fortyfikacji. Mobilne i silnie uzbrojone oddziały Wehrmachtu w ciągu trzech pierwszych dni wygrały
      wojnę
      graniczną, przełamując obronę przeciwnika i spychając jego armie w głąb kraju.
      Nadzieję w serca Polaków wlała decyzja rządów Francji i Wielkiej Brytanii. 3 września, zgodnie z sojuszniczymi
      zobowiązaniami, wypowiedziały one Niemcom wojnę.
        Do spodziewanej ofensywy na froncie zachodnim jednak nie doszło Powód? Alianci uznali, że nie są jeszcze
      gotowi,
      by stawić czoła nazistowskiej machinie wojennej. Nie wszyscy ich
      dowódcy podzielali jednak tę opinię. „Można było wejść jak w masło w pozycje niemieckie. Można było rozstrzygnąć
      wojnę w 1939 roku. Wystarczyło trochę zdecydowania i charakteru – ubolewał po latach francuski marszałek Alphonse
      Juin. – Co za hańba, co za wstyd, co za głupota zarazem. To idealna sytuacja, wręcz modelowa, w której można było
      pobić przeciwnika, wychodząc na jego tyły”.<br>
      Tymczasem mimo rosnącej przewagi przeciwnika, Polacy bili się dzielnie. W nocy z 9 na 10 września zdołali nawet
      wyprowadzić skuteczne uderzenie. Dowodzone przez gen. Tadeusza Kutrzebę Armie „Poznań” i „Pomorze” wyprowadziły
      nieoczekiwany cios w skrzydło 8 Armii gen. Johannesa Blaskowitza. Krwawa
        bitwa
        nad Bzurą na kilkadziesiąt godzin
      wyhamowała niemieckie natarcie na Warszawę. Polskie nadzieje ostatecznie jednak pogrzebane zostały 17 września,
      kiedy to ze wschodu
        zaatakowała Armia Czerwona .npPod koniec września padła Warszawa, 2 października skapitulował Rejon
        Umocniony
        Hel , a cztery dni później Samodzielna Grupa
        Operacyjna
        Polesie dowodzona przez gen. Franciszka
      Kleeberga. Wojna obronna została przegrana, a Polska zniknęła z mapy Europy.</p>
    <h4 class="fw-bold">To zaledwie początek...</h4>
    <p>Straty były ogromne. Wiele miast zostało obróconych w gruzy, liczne wioski puszczone z dymem. Niemcy oraz Sowieci
      pokazali, że dla osiągnięcia militarnych oraz ideologicznych celów, gotowi są łamać
        wszelkie
        zasady prowadzenia wojny Celem ataków regularnie padali cywile. Wystarczy przywołać Wieluń – w chwili nalotu
      w
      miasteczku nie było ani jednego żołnierza, polskie oddziały stacjonowały kilkadziesiąt kilometrów dalej. Jeden ze
      świadków nalotu wspominał
      „Zniszczenia były ogromne (…). Obok szpitala na gałęziach drzewa widziałem zwisające zwłoki. Na ulicach leżało
      sporo
      zabitych i rannych (…). Popłoch panował nie do opisania. Ludzie uciekali z miasta, wielu biegało tylko w
      bieliźnie.
      Zaskoczenie było tak wielkie, że niektórzy odchodzili od zmysłów”. W walkach zginęło 66 tysięcy polskich
      żołnierzy,
      blisko 700 tysięcy dostało się do niewoli.
      Ale Polacy potrafili też zadać ciężkie straty napastnikom, zwłaszcza Niemcom. Planowany przez hitlerowskich
      dowódców
      Blitzkrieg przerodził się w ciężkie, trwające przeszło miesiąc walki. Podczas kampanii wrześniowej Wehrmacht
      stracił
      przeszło 17 tysięcy żołnierzy, 217 czołgów, ponad dziesięć tysięcy różnego typu pojazdów i ogromne zapasy
      amunicji.
      W efekcie musieli przesunąć inwazję na zachodnią Europę. Nieżyjący już prof. Paweł Wieczorkiewicz zauważał:
      „Hitler,
      oceniając wnioski płynące z kampanii, stwierdził, że «gdyby Polska miała broń przeciwpancerną, zwycięski pochód
      nie
      byłby możliwy». Istotnie, kliny czołgowe, a także lotnictwo, odegrały w niej rolę decydującą. O druzgocącej
      przewadze materiałowej Wehrmachtu (…) stanowiła nie tyle jakość posiadanego sprzętu (…), co jego ilość i doktryna
      zmasowanego użycia”. W ocenie historyka, podczas walk obronnych, wbrew kolportowanym później opiniom, nieźle
      spisało
      się niewielkie polskie lotnictwo oraz kawaleria. „Mobilna i nieźle wyposażona w działka przeciwpancerne była w
      istocie najbardziej skutecznym środkiem hamowania postępów niemieckich czołgów. Jednostki kawalerii potrafiły w
      późniejszych etapach kampanii skuteczniej i dłużej wymykać się przeciwnikowi niż o wiele bardziej znużona
      fizycznie
      odwrotowymi marszami piechota”.<br>
      Po pobiciu Polski, Hitler zwrócił się do Francji i Wielkiej Brytanii z ofertą pokoju. Alianci jednak ją odrzucili.
      Wojna dopiero się zaczynała, a polscy żołnierze mieli podczas niej zaznaczyć swoją obecność na wszystkich niemal
      frontach.
    </p>
